export const triggerAddBeadToScene = (bead: Bead, scene: AppThreeScene) => {
  const beadType = bead.type;
  //  const beadTHREEObject = new THREE.Group();
  const beadModel = getModelFromBeadType(beadType);

  if (beadModel === undefined) {
    console.warn("beadModel not found for type: ", beadType, bead.id);
    return;
  }

  const object = scene.getObjectByUserDataProperty("id", bead.id);

  if (object !== undefined) {
    console.warn("bead already in scene", bead.id);
    return;
  }

  beadModel.traverse((child) => {
    child.userData = getThreeUserDataFromBead(bead, BeadChildSuffix.ANY);
    if (child instanceof THREE.Mesh) {
      if (bead.material === BeadMaterialTypes.Mat) {
        child.material = new THREE.MeshPhysicalMaterial({
          reflectivity: 1,
          color: bead.color,
        });
      } else if (bead.material === BeadMaterialTypes.Shiny) {
        child.material = new THREE.MeshPhongMaterial({
          color: bead.color,
        });
      } else {
        console.warn("unknow BeadMaterialTypes:", bead.material);
      }

      // child.material = new THREE.MeshLambertMaterial({
      //   color: bead.color,
      // });

      // child.material = new THREE.MeshStandardMaterial({
      //   color: bead.color,
      //   metalness: 0,
      //   roughness: 0.5,
      // });

      // child.material = new THREE.MeshDepthMaterial();

      // child.material = new THREE.MeshPhysicalMaterial({
      //   flatShading: true,
      //   reflectivity: 1,
      //   color: bead.color,
      // });
    }
    // child.layers.set(2);
    if ((child as any).geometry) {
      (child as any).geometry.computeBoundingBox();
    }
  });

  const bbox = getModelBBoxFromBeadType(beadType);
  let width = 1;
  let height = 1;
  let depth = 1;
  if (bbox) {
    width = Math.abs(bbox.max.x - bbox.min.x);
    height = Math.abs(bbox.max.y - bbox.min.y);
    depth = Math.abs(bbox.max.z - bbox.min.z);
  }

  beadModel.scale.x = bead.width / width;
  beadModel.scale.y = bead.height / height;
  beadModel.scale.z = bead.depth / depth;

  beadModel.rotation.x = THREE.MathUtils.DEG2RAD * bead.rotateX;
  beadModel.rotation.y = THREE.MathUtils.DEG2RAD * bead.rotateY;
  beadModel.rotation.z = THREE.MathUtils.DEG2RAD * bead.rotateZ;

  beadModel.position.x = bead.position.x;
  beadModel.position.y = bead.position.y;
  beadModel.position.z = bead.position.z;

  beadModel.userData = getThreeUserDataFromBead(bead);
  // beadModel.layers.set(2);

  // beadTHREEObject.add(beadModel);

  scene.add(beadModel);
  return beadModel;
};

export const triggerGroupToScene = (group: Group) => {
  const geometry = new THREE.PlaneGeometry(1, 1, 1, 1);
  const material = new THREE.MeshBasicMaterial({
    color: "rgb(0,0,0)",
    side: THREE.FrontSide,
    opacity: 0.2,
    transparent: true,
  });
  const groupBgThree = new THREE.Mesh(geometry, material);
  groupBgThree.userData = getThreeUserDataFromGroup(group, GroupChildSuffix.BG);

  const groupThree = new THREE.Group();
  groupThree.userData = getThreeUserDataFromGroup(group, GroupChildSuffix.MAIN);
  groupThree.position.x = group.position.x;
  groupThree.position.y = group.position.y;
  groupThree.position.z = -400;
  groupThree.scale.x = group.width;
  groupThree.scale.y = group.height;
  groupThree.scale.z = 1;

  // groupThree.rotation.x = THREE.MathUtils.DEG2RAD * group.rotateX;
  // groupThree.rotation.y = THREE.MathUtils.DEG2RAD * group.rotateY;
  // groupThree.rotation.z = THREE.MathUtils.DEG2RAD * group.rotateZ;
  groupThree.add(groupBgThree);

  appThree.scene.add(groupThree);
};

