export const triggerDeleteObjectIds = (
  objects: { id: string; objectType: ObjectTypes }[]
) => {
  objects.forEach((obj) => {
    const name = getStateObjectFromObjectId(obj.id, obj.objectType);
    if (name === undefined) {
      console.warn(
        "triggerDeleteObjectIds:: name cant be created",
        obj.id,
        obj.objectType
      );
      return;
    }
    const object = appThree.scene.getObjectByUserDataProperty("name", name);
    if (object === undefined) {
      console.warn("triggerDeleteObjectIds:: object not found by name", name);
      return;
    }

    appThree.scene.remove(object);

    const __object = appThree.scene.getObjectByUserDataProperty("name", name);

    if (__object !== undefined) {
      console.warn(
        "triggerDeleteObjectIds:: try double - object found by name in scene ",
        name
      );
    }
  });
};

export const triggerDeleteBeadIds = (beadIds: string[]) => {
  beadIds.forEach((beadId) => {
    while (true) {
      const name = getThreeNameFromBeadId(beadId);
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        break;
      }
      appThree.scene.remove(object);
    }
    while (true) {
      const name = getThreeNameFromBeadId(beadId, BeadChildSuffix.ANY);
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        break;
      }
      appThree.scene.remove(object);
    }
  });
};

