//     getObjectByUserDataProperty: (
//       name: string,
//       value: any
//     ) => undefined | Object3D;
//   }
// }
// source: https://github.com/mrdoob/three.js/blob/0fbc8afb348198e4924d9805d1d4be5869264418/src/core/Object3D.js#L85
// source: https://discourse.threejs.org/t/getobject-by-any-custom-property-present-in-userdata-of-object/3378
(THREE.Object3D.prototype as any).getObjectByUserDataProperty = function (
  name: string,
  value: any
) {
  if (this.userData[name] === value) return this;

  for (var i = 0, l = this.children.length; i < l; i++) {
    var child = this.children[i];
    var object = child.getObjectByUserDataProperty(name, value);

    if (object !== undefined) {
      return object;
    }
  }

  return undefined;
};
