export const getModelFromBeadType = (type: BeadTypes) => {
  let beadModel: THREE.Object3D | undefined;

  if (type === BeadTypes.TOHO_MIYUKI) {
    if (base3dModels.toho_miyuki.model === null) {
      console.error("triggerAddBeadToScene: 3dModel is not loaded diamond");
      return beadModel;
    }
    beadModel = base3dModels.toho_miyuki.model.clone();
  }

  if (type === BeadTypes.TOHO_15) {
    if (base3dModels.toho_15.model === null) {
      console.error("triggerAddBeadToScene: 3dModel is not loaded baseBead");
      return beadModel;
    }
    beadModel = base3dModels.toho_15.model.clone();
  }

  if (type === BeadTypes.TOHO_SPHERE) {
    if (base3dModels.toho_sphere.model === null) {
      console.error("triggerAddBeadToScene: 3dModel is not loaded baseBead");
      return beadModel;
    }
    beadModel = base3dModels.toho_sphere.model.clone();
  }

  return beadModel;
};

export const getModelBBoxFromBeadType = (type: BeadTypes) => {
  if (type === BeadTypes.TOHO_MIYUKI) {
    return base3dModels.toho_miyuki.bbox;
  }

  if (type === BeadTypes.TOHO_15) {
    return base3dModels.toho_15.bbox;
  }

  if (type === BeadTypes.TOHO_SPHERE) {
    return base3dModels.toho_sphere.bbox;
  }

  return null;
};
