import { AddObjectHandler } from "./EventHandlers/AddObjectHandler";
import { AppEvents, AppEventType } from "./AppEventType";
import { getStore } from "./store";
import { RemoveObjectHandler } from "./EventHandlers/RemoveObjectHandler";
import { ChangeSelectObjectsHandler } from "./EventHandlers/ChangeSelectObjectsHandler";
import { ChangeObjectsPropertyHandler } from "./EventHandlers/ChangeObjectsPropertyHandler";
import { getHistory } from "./AppHistory";
import { ChangeUiSettingsHandler } from "./EventHandlers/ChangeUiSettingsHandler";
import { ClipboardCopyHandler } from "./EventHandlers/ClipboardCopyHandler";
import { ClipboardCutHandler } from "./EventHandlers/ClipboardCutHandler";
import { ClipboardPasteHandler } from "./EventHandlers/ClipboardPasteHandler";
import { AddMultipleBeadsHandler } from "./EventHandlers/AddMultipleBeadsHandler";
import { ChangeUiSettingsZoomHandler } from "./EventHandlers/ChangeUiSettingsZoomHandler";
import { ChangeUiSettingsSidebarHandler } from "./EventHandlers/ChangeUiSettingsSidebarHandler";
import { ChangeObjectsAlignmentHandler } from "./EventHandlers/ChangeObjectsAlignmentHandler";
import { DuplicateObjectsHandler } from "./EventHandlers/DuplicateObjectsHandler";
import { GroupObjectsHandler } from "./EventHandlers/GroupObjectsHandler";
import { UngroupObjectsHandler } from "./EventHandlers/UngroupObjectsHandler";

export const sendEvent = (event: AppEvents, saveToHistory: boolean = true) => {
  console.log(
    "dispatchEvent",
    event.name,
    event.payload,
    "saveToHistory",
    saveToHistory
  );

  const history = getHistory();
  const store = getStore();
  if (event.name === AppEventType.AddObjects) {
    new AddObjectHandler(event, store, history, saveToHistory).run();
  } else if (event.name === AppEventType.RemoveObjects) {
    new RemoveObjectHandler(event, store, history, saveToHistory).run();
  } else if (event.name === AppEventType.ChangeSelectObjects) {
    new ChangeSelectObjectsHandler(event, store, history, saveToHistory).run();
  } else if (event.name === AppEventType.ChangeObjectsProperty) {
    new ChangeObjectsPropertyHandler(
      event,
      store,
      history,
      saveToHistory
    ).run();
  } else if (event.name === AppEventType.ChangeUiSettings) {
    new ChangeUiSettingsHandler(event, store, history, saveToHistory).run();
  } else if (event.name === AppEventType.ClipboardCopy) {
    new ClipboardCopyHandler(event, store, history, saveToHistory).run();
  } else if (event.name === AppEventType.ClipboardCut) {
    new ClipboardCutHandler(event, store, history, saveToHistory).run();
  } else if (event.name === AppEventType.ClipboardPaste) {
    new ClipboardPasteHandler(event, store, history, saveToHistory).run();
  } else if (event.name === AppEventType.AddMultipleBeads) {
    new AddMultipleBeadsHandler(event, store, history, saveToHistory).run();
  } else if (event.name === AppEventType.ChangeUiSettingsZoom) {
    new ChangeUiSettingsZoomHandler(event, store, history, saveToHistory).run();
  } else if (event.name === AppEventType.ChangeUiSettingsSidebar) {
    new ChangeUiSettingsSidebarHandler(
      event,
      store,
      history,
      saveToHistory
    ).run();
  } else if (event.name === AppEventType.ChangeObjectsAlignment) {
    new ChangeObjectsAlignmentHandler(
      event,
      store,
      history,
      saveToHistory
    ).run();
  } else if (event.name === AppEventType.DuplicateObjects) {
    new DuplicateObjectsHandler(event, store, history, saveToHistory).run();
  } else if (event.name === AppEventType.GroupObjects) {
    new GroupObjectsHandler(event, store, history, saveToHistory).run();
  } else if (event.name === AppEventType.UngroupObjects) {
    new UngroupObjectsHandler(event, store, history, saveToHistory).run();
  } else {
    console.warn("event handler not found to name", event);
  }

  return event;
};
