import { debounce } from "lodash";
import { AppData } from "./AppData";
import { AppEvents, AppEventType } from "./controllers/AppEventType";
import { AppObject } from "./editorObjects/Editor";

let apiHost: string;
let headers: { [key: string]: any } = {
  "Content-Type": "application/json",
};

export const setApiFromAppData = (data: AppData) => {
  apiHost = data.apiHost;
  headers["Authorization"] = "Bearer " + data.token;
};

const handleError = (err: any, reject: any) => {
  console.error("request error", err);
  reject("err");
};

export const prepareBoardsList = (): Promise<{
  boards: {
    uuid: string;
    name: string;
  }[];
}> => {
  return new Promise((resolve, reject) => {
    return fetch(apiHost + "api/boards/list", {
      method: "GET",
      headers,
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.statusCode && response.statusCode !== 200) {
          reject(response.message);
        }
        resolve({
          boards: response.boards,
        });
      })
      .catch((err) => handleError(err, reject));
  });
};

export const fetchBoard = (
  id: string
): Promise<{ board: { uuid: string; name: string }; objects: AppObject[] }> => {
  return new Promise((resolve, reject) => {
    return fetch(apiHost + "api/boards/one/" + id, {
      method: "GET",
      headers,
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.statusCode && response.statusCode !== 200) {
          reject(response.message);
        }
        // console.log("co tam jest??", response.objects);
        resolve({
          board: response.board,
          objects: Object.keys(response.objects).map(
            (key) => response.objects[key]
          ),
        });
      })
      .catch((err) => handleError(err, reject));
  });
};

export const createBoard = (
  name?: string
): Promise<{ board: { uuid: string; name: string } }> => {
  return new Promise((resolve, reject) => {
    return fetch(apiHost + "api/boards/create", {
      method: "POST",
      headers,
      body: JSON.stringify({ name: name ?? "" }),
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.statusCode && response.statusCode !== 200) {
          reject(response.message);
        }
        resolve({
          board: response.board,
        });
      })
      .catch((err) => handleError(err, reject));
  });
};
export const sendBoardBatchEvents = (
  id: string,
  body: string
): Promise<{ status: boolean }> => {
  return new Promise((resolve, reject) => {
    return fetch(apiHost + "api/boards/events/" + id, {
      method: "POST",
      headers,
      body,
    })
      .then((res) => res.json())
      .then((response) => {
        if (response.statusCode && response.statusCode !== 200) {
          reject(response.message);
        }
        resolve({
          status: response.statusCode,
        });
      })
      .catch((err) => handleError(err, reject));
  });
};

let tempBoardId: string | undefined;
let sendingEnabled: boolean = false;
let sendingIntervalMs: number = 500;
let sendingInterval: any = undefined;
let lastSendTimestamp: any = new Date().getTime();
let toSendEventsQueue: SaveEvent[] = [];

export enum SaveEventType {
  CREATE = "CREATE",
  UPDATE = "UPDATE",
  DELETE = "DELETE",
}
export interface SaveEvent {
  action: SaveEventType;
  object: AppObject;
  createdAt: string;
}

export const notifyBackend = (events: SaveEvent[]) => {
  if (sendingEnabled === true) {
    if (events.length > 0) {
      events.forEach((event) => {
        toSendEventsQueue.push(event);
      });
      debouncedSend();
    }
  }
};
const debouncedSend = debounce(
  () => triggerSendBatchEvents(),
  sendingIntervalMs,
  { trailing: true }
);

export const startBatchQueue = (id: string) => {
  sendingEnabled = true;
  tempBoardId = id;
  lastSendTimestamp = new Date().getTime();
  // sendingInterval = setInterval(debouncedSend, 250);
};

const triggerSendBatchEvents = () => {
  const now = new Date().getTime();
  console.log(
    "triggerSendBatchEvents: diff: ",
    now - lastSendTimestamp,
    toSendEventsQueue.length > 0
  );
  if (toSendEventsQueue.length > 0 && tempBoardId !== undefined) {
    const body = JSON.stringify({ changes: toSendEventsQueue });
    toSendEventsQueue = [];
    sendBoardBatchEvents(tempBoardId, body);
  }
  lastSendTimestamp = now;
};

export const stopBatchQueue = (id: string) => {
  sendingEnabled = false;
  if (tempBoardId !== undefined) {
    tempBoardId = undefined;
  }
  if (sendingInterval !== undefined) {
    clearInterval(sendingInterval);
  }
  if (toSendEventsQueue.length > 0) {
    toSendEventsQueue = [];
  }
};

if (!Date.prototype.toISOString) {
  (function () {
    function pad(number) {
      if (number < 10) {
        return "0" + number;
      }
      return number;
    }

    Date.prototype.toISOString = function () {
      return (
        this.getUTCFullYear() +
        "-" +
        pad(this.getUTCMonth() + 1) +
        "-" +
        pad(this.getUTCDate()) +
        "T" +
        pad(this.getUTCHours()) +
        ":" +
        pad(this.getUTCMinutes()) +
        ":" +
        pad(this.getUTCSeconds()) +
        "." +
        (this.getUTCMilliseconds() / 1000).toFixed(3).slice(2, 5) +
        "Z"
      );
    };
  })();
}
