import produce from "immer";
import { cloneDeep } from "lodash";
import {
  addObjectByObjectType,
  triggerDeleteObjectIds,
  updateObjectPosition,
} from "../../threejs/eventEmitterHandlers";
import {
  ChangeObjectsProperty,
  AppEventType,
  ChangeObject,
} from "../AppEventType";
import { ChangeObjectsPropertyHistory } from "../AppHistory";
import { refreshReactState } from "../refreshReactState";
import { setStore, Store } from "../store";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import dayjs from "dayjs";
import {
  AppObject,
  Bead,
  Group,
  groupIdKey,
  ObjectTypes,
  Point,
  Region,
} from "../../editorObjects/Editor";
import { notifyBackend, SaveEventType } from "../../ApiService";
import { WritableDraft } from "immer/dist/internal";
import { buildBead } from "../../editorObjects/Bead";
import THREE from "three";
import { contain } from "../../editorObjects/Point";

type ObjectsMap = { [key: string]: AppObject };

export class ChangeObjectsPropertyHandler extends StoreObjectHandler<ChangeObjectsProperty> {
  run() {
    const added: AppObject[] = [];
    const updated: ObjectsMap = {};
    const removed: AppObject[] = [];
    const { changes } = this.event.payload;
    const historyChanges: ChangeObjectsProperty["payload"]["changes"] = [];
    let newStore = produce(this.store, (draft) => {
      changes.forEach((_singleChange) => {
        if (draft.objects[_singleChange.id] === undefined) {
          console.warn("no object found in store", _singleChange.id);
          return;
        }
        const singleHistoryChange: ChangeObject = {
          id: _singleChange.id,
          properties: {},
          relatedChanges: [],
          relatedCreateBeads: [],
          relatedDeleteIds: [],
        };
        const keyNames = Object.keys(_singleChange.properties);
        if (singleHistoryChange !== undefined) {
          keyNames.forEach((key) => {
            singleHistoryChange.properties[key] = cloneDeep(
              draft.objects[_singleChange.id][key]
            );
          });
        }
        keyNames.forEach((key) => {
          draft.objects[_singleChange.id][key] = cloneDeep(
            _singleChange.properties[key]
          );
        });

        if (_singleChange.relatedChanges) {
          _singleChange.relatedChanges.forEach((_singeRelatedChange) => {
            if (draft.objects[_singeRelatedChange.id] === undefined) {
              console.warn("no object found in store", _singeRelatedChange.id);
              return;
            }
            const keyNames = Object.keys(_singeRelatedChange.properties);
            keyNames.forEach((key) => {
              draft.objects[_singeRelatedChange.id][key] = cloneDeep(
                _singeRelatedChange.properties[key]
              );
            });
          });
        }

        historyChanges.push(singleHistoryChange);
      });

      changes.forEach((_singleChange, index) => {
        if (draft.objects[_singleChange.id] === undefined) {
          console.warn("no object found in store", _singleChange.id);
          return;
        }
        const objectType = draft.objects[_singleChange.id].objectType;
        const singleHistoryChange = historyChanges[index];

        if (objectType === ObjectTypes.Bead) {
          this.whenBead(
            _singleChange,
            draft,
            singleHistoryChange,
            added,
            updated,
            removed
          );
        }

        if (objectType === ObjectTypes.Region) {
          this.whenRegion(
            _singleChange,
            draft,
            singleHistoryChange,
            added,
            updated,
            removed
          );
        }
      });
    });

    if (this.saveToHistory) {
      const historyEvent: ChangeObjectsPropertyHistory = {
        do: this.event,
        redo: {
          name: AppEventType.ChangeObjectsProperty,
          payload: {
            changes: this.event.payload.changes.map(
              ({ id, properties, relatedChanges }) => {
                const index = historyChanges.findIndex((e) => e.id === id);
                const relatedCreateBeads =
                  index !== -1 ? historyChanges[index].relatedCreateBeads : [];

                const relatedDeleteIds =
                  index !== -1 ? historyChanges[index].relatedDeleteIds : [];

                return {
                  id,
                  properties,
                  relatedChanges,
                  relatedCreateBeads,
                  relatedDeleteIds,
                };
              }
            ),
          },
        },
        undo: {
          name: AppEventType.ChangeObjectsProperty,
          payload: { changes: historyChanges },
        },
        createdAt: dayjs().toISOString(),
      };
      this.history.push(historyEvent);
    }

    newStore = this.getStoreWithHistory(newStore);

    // @INFO: CHANGES IN THREEJS
    changes.forEach((_singleChange) => {
      this.applyChangesToThree(_singleChange, newStore);
      notifyBackend([
        {
          action: SaveEventType.UPDATE,
          createdAt: new Date().toISOString(),
          object: newStore.objects[_singleChange.id],
        },
      ]);

      if (_singleChange.relatedChanges) {
        _singleChange.relatedChanges.forEach((_singeRelatedChange) => {
          this.applyChangesToThree(_singeRelatedChange, newStore);
          notifyBackend([
            {
              action: SaveEventType.UPDATE,
              createdAt: new Date().toISOString(),
              object: newStore.objects[_singeRelatedChange.id],
            },
          ]);
        });
      }
    });

    if (added.length > 0) {
      added.forEach((obj) => {
        addObjectByObjectType(obj);
      });

      notifyBackend(
        added.map((object) => {
          return {
            action: SaveEventType.CREATE,
            createdAt: new Date().toISOString(),
            object,
          };
        })
      );
    }

    const updatedKeys = Object.keys(updated);
    if (updatedKeys.length > 0) {
      triggerDeleteObjectIds(
        updatedKeys.map((id) => {
          return {
            id,
            objectType: newStore.objects[id].objectType,
          };
        })
      );

      updatedKeys.map((id) => {
        addObjectByObjectType(newStore.objects[id]);
      });

      notifyBackend(
        updatedKeys.map((id) => {
          return {
            action: SaveEventType.UPDATE,
            createdAt: new Date().toISOString(),
            object: newStore.objects[id],
          };
        })
      );
    }

    if (removed.length > 0) {
      triggerDeleteObjectIds(
        removed.map((obj) => {
          return { id: obj.id, objectType: obj.objectType };
        })
      );
      notifyBackend(
        removed.map((object) => {
          return {
            action: SaveEventType.DELETE,
            createdAt: new Date().toISOString(),
            object,
          };
        })
      );
    }

    newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(newStore);
    refreshReactState(newStore);
  }

  applyChangesToThree(_singleChange: ChangeObject, _store: Store) {
    if (_store.objects[_singleChange.id] === undefined) {
      console.warn("no object found in store", _singleChange.id);
      return;
    }
    const object = _store.objects[_singleChange.id];
    const inGroup =
      object[groupIdKey] !== undefined && object[groupIdKey].length > 0;

    const positionKey: keyof AppObject = "position";
    // const objectType = _store.objects[_singleChange.id].objectType;
    const keyNames = Object.keys(_singleChange.properties);
    const isPositionOnly =
      keyNames.length === 1 && keyNames.includes(positionKey) === true;
    if (isPositionOnly && inGroup === false) {
      updateObjectPosition(
        _singleChange.id,
        object.objectType,
        object.position.x,
        object.position.y
      );
    }
    // else if (objectType === ObjectTypes.Region) {
    // const isShapeOnly =
    // keyNames.length === 1 && keyNames.includes("shape") === true;
    // }
    else {
      triggerDeleteObjectIds([
        {
          id: _singleChange.id,
          objectType: object.objectType,
        },
      ]);

      addObjectByObjectType(object);

      if (inGroup) {
        if (
          object[groupIdKey] !== undefined &&
          _store.objects[object[groupIdKey]]
        ) {
          (_store.objects[object[groupIdKey]] as Group).childrenIds.forEach(
            (id) => {
              const obj = _store.objects[id];
              triggerDeleteObjectIds([
                {
                  id: id,
                  objectType: obj.objectType,
                },
              ]);
              addObjectByObjectType(obj);
            }
          );
        }
      }
    }
  }

  whenBead(
    _singleChange: ChangeObject,
    draft: WritableDraft<Store>,
    singleHistoryChange: ChangeObject,
    added: AppObject[],
    updated: ObjectsMap,
    removed: AppObject[]
  ) {
    const positionKey: keyof AppObject = "position";
    const regionIdKey: keyof Bead = "regionId";
    const childrenGridIdsKey: keyof Region = "childrenGridIds";
    const keyNames = Object.keys(_singleChange.properties);

    if (keyNames.includes(positionKey)) {
      let isBeadFoundRegion = false;
      const bead = draft.objects[_singleChange.id] as Bead;
      const regionId = (draft.objects[_singleChange.id] as Bead).regionId;

      if (regionId !== undefined) {
        const region = draft.objects[regionId] as Region;
        const regionTopLeftX = region.position.x - region.width / 2;
        const regionTopLeftY = region.position.y - region.height / 2;

        const containBead = contain(
          {
            x: regionTopLeftX,
            y: regionTopLeftY,
            z: 0,
          },
          {
            x: region.position.x + region.width / 2,
            y: region.position.y + region.height / 2,
            z: 0,
          },
          {
            x: bead.position.x,
            y: bead.position.y,
            z: 0,
          }
        );

        if (containBead === true) {
          const cellW = region.width / region.columns;
          const cellH = region.height / region.rows;
          const columnIndex = Math.floor(
            (bead.position.x - regionTopLeftX) / cellW
          );
          const rowIndex = Math.floor(
            (bead.position.y - regionTopLeftY) / cellH
          );
          const newIndex = rowIndex * region.columns + columnIndex;

          const oldIndex = region.childrenGridIds.indexOf(bead.id);
          if (oldIndex !== -1 && oldIndex !== newIndex) {
            draft.objects[regionId][childrenGridIdsKey][oldIndex] = undefined;
          }
          draft.objects[regionId][childrenGridIdsKey][newIndex] = bead.id;
          isBeadFoundRegion = true;
          if (region.snapBeadsToCell === true) {
            const cellW = region.width / region.columns;
            const cellH = region.height / region.rows;
            const offsetX = columnIndex * cellW;
            const offsetY = rowIndex * cellH;
            const startX = region.position.x - region.width / 2;
            const startY = region.position.y - region.height / 2;
            const posX = startX + offsetX + cellW / 2;
            const posY = startY + offsetY + cellH / 2;
            draft.objects[bead.id].position.x = posX;
            draft.objects[bead.id].position.y = posY;
          }
        }
      }

      if (isBeadFoundRegion === false) {
        Object.keys(draft.objects).forEach((regionId) => {
          if (draft.objects[regionId].objectType !== ObjectTypes.Region) {
            return;
          }
          const region = draft.objects[regionId] as Region;
          const regionTopLeftX = region.position.x - region.width / 2;
          const regionTopLeftY = region.position.y - region.height / 2;

          const containBead = contain(
            {
              x: regionTopLeftX,
              y: regionTopLeftY,
              z: 0,
            },
            {
              x: region.position.x + region.width / 2,
              y: region.position.y + region.height / 2,
              z: 0,
            },
            {
              x: bead.position.x,
              y: bead.position.y,
              z: 0,
            }
          );

          if (containBead === false) {
            return;
          }

          const cellW = region.width / region.columns;
          const cellH = region.height / region.rows;
          const columnIndex = Math.floor(
            (bead.position.x - regionTopLeftX) / cellW
          );
          const rowIndex = Math.floor(
            (bead.position.y - regionTopLeftY) / cellH
          );

          const newIndex = rowIndex * region.columns + columnIndex;

          if (region.childrenGridIds[newIndex] === undefined) {
            const currentIndex = region.childrenGridIds.indexOf(bead.id);
            if (currentIndex !== -1 && currentIndex !== newIndex) {
              draft.objects[regionId][childrenGridIdsKey][
                currentIndex
              ] = undefined;
            }
            draft.objects[regionId][childrenGridIdsKey][newIndex] = bead.id;

            updated[regionId] = cloneDeep(draft.objects[regionId]);

            if (draft.objects[bead.id][regionIdKey] !== undefined) {
              const oldRegionId = draft.objects[bead.id][regionIdKey];
              const oldRegion = draft.objects[oldRegionId] as Region;
              const currentIndex = oldRegion.childrenGridIds.indexOf(bead.id);
              if (currentIndex !== -1) {
                draft.objects[oldRegionId][childrenGridIdsKey][
                  currentIndex
                ] = undefined;
                updated[oldRegionId] = cloneDeep(draft.objects[oldRegionId]);
              }
            }

            draft.objects[bead.id][regionIdKey] = region.id;
            isBeadFoundRegion = true;

            if (region.snapBeadsToCell === true) {
              const cellW = region.width / region.columns;
              const cellH = region.height / region.rows;
              const offsetX = columnIndex * cellW;
              const offsetY = rowIndex * cellH;
              const startX = region.position.x - region.width / 2;
              const startY = region.position.y - region.height / 2;
              const posX = startX + offsetX + cellW / 2;
              const posY = startY + offsetY + cellH / 2;
              draft.objects[bead.id].position.x = posX;
              draft.objects[bead.id].position.y = posY;
            }
          } else if (region.childrenGridIds[newIndex] === bead.id) {
            if (region.snapBeadsToCell === true) {
              const cellW = region.width / region.columns;
              const cellH = region.height / region.rows;
              const offsetX = columnIndex * cellW;
              const offsetY = rowIndex * cellH;
              const startX = region.position.x - region.width / 2;
              const startY = region.position.y - region.height / 2;
              const posX = startX + offsetX + cellW / 2;
              const posY = startY + offsetY + cellH / 2;
              draft.objects[bead.id].position.x = posX;
              draft.objects[bead.id].position.y = posY;
              isBeadFoundRegion = true;
            }
          }
        });
      }

      if (isBeadFoundRegion === false) {
        if (regionId !== undefined) {
          const region = draft.objects[regionId] as Region;
          const currentIndex = region.childrenGridIds.indexOf(bead.id);
          if (currentIndex !== -1) {
            draft.objects[regionId][childrenGridIdsKey][
              currentIndex
            ] = undefined;

            updated[regionId] = cloneDeep(draft.objects[regionId]);
          }

          (draft.objects[_singleChange.id] as Bead).regionId = undefined;
        }
      }
    }
  }

  whenRegion(
    _singleChange: ChangeObject,
    draft: WritableDraft<Store>,
    singleHistoryChange: ChangeObject,
    added: AppObject[],
    updated: ObjectsMap,
    removed: AppObject[]
  ) {
    const keyNames = Object.keys(_singleChange.properties);
    const shapeKey: keyof Region = "shape";
    const autoFillBeadsKey: keyof Region = "autoFillBeads";
    const snapBeadsToCellKey: keyof Region = "snapBeadsToCell";
    const rowsKey: keyof Region = "rows";
    const columnsKey: keyof Region = "columns";
    const positionKey: keyof Region = "position";

    if (
      _singleChange.relatedCreateBeads !== undefined &&
      _singleChange.relatedCreateBeads.length > 0
    ) {
      const region = draft.objects[_singleChange.id] as Region;
      const relatedCreateBeadsIds: string[] = [];

      _singleChange.relatedCreateBeads.forEach((bead) => {
        if (draft.objects[bead.id] === undefined) {
          console.warn("bead not found", bead.id);
          return;
        }

        relatedCreateBeadsIds.push(bead.id);

        removed.push(cloneDeep(bead));

        delete draft.objects[bead.id];
      });

      region.childrenGridIds.forEach((id, index) => {
        if (id !== undefined && relatedCreateBeadsIds.includes(id)) {
          (draft.objects[region.id] as Region).childrenGridIds[
            index
          ] = undefined;
        }
      });
    }

    if (keyNames.includes(rowsKey) || keyNames.includes(columnsKey)) {
      const oldRows = singleHistoryChange.properties[rowsKey];
      const oldColumns = singleHistoryChange.properties[columnsKey];
      const region = draft.objects[_singleChange.id] as Region;
      const newRows = region.rows;
      const newColumns = region.columns;
      const newSize = region.rows * region.columns;
      const oldSize = region.childrenGridIds.length;
      const newChildrenGridIds = Array(newSize).fill(undefined);
      const autoFillBeadsEnabled = region[autoFillBeadsKey] === true;

      const toDeleteBeadIds: string[] = [];
      for (let i = 0; i < region.childrenGridIds.length; i++) {
        const oldIndex = i;
        const oldCurrentRow = Math.floor(i / oldColumns);
        const oldCurrentCol = Math.floor(i % oldColumns);

        let newIndex: number = oldCurrentCol + oldCurrentRow * newColumns;

        if (oldCurrentCol >= newColumns || oldCurrentRow >= newRows) {
          newIndex = newSize;

          if (autoFillBeadsEnabled) {
            const beadId = region.childrenGridIds[oldIndex];
            if (beadId !== undefined && draft.objects[beadId] !== undefined) {
              toDeleteBeadIds.push(beadId);
              singleHistoryChange.relatedDeleteIds!.push(beadId);
            }
          }
        }

        if (newIndex < newSize) {
          newChildrenGridIds[newIndex] = region.childrenGridIds[oldIndex];
        }
      }

      toDeleteBeadIds.forEach((beadId) => {
        if (draft.objects[beadId] === undefined) {
          console.warn("bead not found", beadId);
          return;
        }

        removed.push(cloneDeep(draft.objects[beadId]));
        delete draft.objects[beadId];
      });

      (draft.objects[region.id] as Region).childrenGridIds = newChildrenGridIds;
    }

    if (keyNames.includes(shapeKey)) {
      // let fistObjectId: string | undefined = undefined;
      // Object.keys(draft.objects).forEach((id) => {
      //   if (draft.objects[id].objectType === ObjectTypes.Bead) {
      //     fistObjectId = id;
      //     return false;
      //   }
      // });
      // if (fistObjectId) {
      // singleHistoryChange.relatedChanges = [
      //   {
      //     id: fistObjectId,
      //     properties: {
      //       position: {
      //         x: -10,
      //         y: -10,
      //       },
      //     },
      //   },
      // ];
      // }
      // zmiana snap dla zaleznych beadsow
      // zmiany w singleHistoryChange.relatedChanges
    }

    if (
      keyNames.includes(positionKey) ||
      keyNames.includes(snapBeadsToCellKey)
    ) {
      const region = draft.objects[_singleChange.id] as Region;
      const snapBeadsToCellEnabled = region[snapBeadsToCellKey] === true;
      // const autoFillBeadsEnabled = region[autoFillBeadsKey] === true;
      if (snapBeadsToCellEnabled) {
        region.childrenGridIds.forEach((id, index) => {
          if (id !== undefined) {
            const currentRow = Math.floor(index / region.columns);
            const currentCol = Math.floor(index % region.columns);
            const cellW = region.width / region.columns;
            const cellH = region.height / region.rows;
            const offsetX = currentCol * cellW;
            const offsetY = currentRow * cellH;
            const startX = region.position.x - region.width / 2;
            const startY = region.position.y - region.height / 2;
            const posX = startX + offsetX + cellW / 2;
            const posY = startY + offsetY + cellH / 2;
            const oldPosition = cloneDeep(draft.objects[id].position);
            draft.objects[id].position.x = posX;
            draft.objects[id].position.y = posY;

            updated[id] = cloneDeep(draft.objects[id]);

            singleHistoryChange.relatedChanges?.push({
              id,
              properties: {
                position: {
                  x: oldPosition.x,
                  y: oldPosition.y,
                  z: oldPosition.z,
                },
              },
            });
          } else {
            const region = draft.objects[_singleChange.id] as Region;
            const size = region.rows * region.columns;

            let foundObject = false;
            Object.keys(draft.objects).forEach((id) => {
              if (foundObject === true) {
                return;
              }
              if (draft.objects[id].objectType !== ObjectTypes.Bead) {
                return;
              }

              const bead = draft.objects[id] as Bead;

              if (bead.groupId !== undefined) {
                return;
              }

              if (bead.regionId !== undefined) {
                return;
              }

              const currentRow = Math.floor(index / region.columns);
              const currentCol = Math.floor(index % region.columns);
              const cellW = region.width / region.columns;
              const cellH = region.height / region.rows;
              const regionTopLeftX = region.position.x - region.width / 2;
              const regionTopLeftY = region.position.y - region.height / 2;

              const containBead = contain(
                {
                  x: regionTopLeftX + currentCol * cellW,
                  y: regionTopLeftY + currentRow * cellH,
                  z: 0,
                },
                {
                  x: regionTopLeftX + currentCol * cellW + cellW,
                  y: regionTopLeftY + currentRow * cellH + cellH,
                  z: 0,
                },
                {
                  x: bead.position.x,
                  y: bead.position.y,
                  z: 0,
                }
              );

              if (containBead === false) {
                return;
              }

              (draft.objects[_singleChange.id] as Region).childrenGridIds[
                index
              ] = bead.id;
              (draft.objects[bead.id] as Bead).regionId = region.id;

              const offsetX = currentCol * cellW;
              const offsetY = currentRow * cellH;
              const startX = region.position.x - region.width / 2;
              const startY = region.position.y - region.height / 2;
              const posX = startX + offsetX + cellW / 2;
              const posY = startY + offsetY + cellH / 2;
              const oldPosition = cloneDeep(draft.objects[bead.id].position);

              draft.objects[bead.id].position.x = posX;
              draft.objects[bead.id].position.y = posY;

              singleHistoryChange.relatedChanges?.push({
                id: bead.id,
                properties: {
                  position: {
                    x: oldPosition.x,
                    y: oldPosition.y,
                    z: oldPosition.z,
                  },
                  regionId: undefined,
                },
              });

              updated[bead.id] = cloneDeep(draft.objects[bead.id]);
              foundObject = true;
            });

            // updated.push(cloneDeep(draft.objects[region.id]));
          }
        });
      } else {
        const region = draft.objects[_singleChange.id] as Region;
        const size = region.rows * region.columns;
        const newChildrenGridIds = Array(size).fill(undefined);
        let emptyCount = size;
        const keys = Object.keys(draft.objects);
        for (let i = 0; i < keys.length; i++) {
          if (emptyCount <= 0) {
            break;
          }

          const id = keys[i];

          if (draft.objects[id].objectType !== ObjectTypes.Bead) {
            continue;
          }

          const bead = draft.objects[id] as Bead;

          if (bead.groupId !== undefined) {
            continue;
          }

          if (bead.regionId !== region.id) {
            continue;
          }

          const regionTopLeftX = region.position.x - region.width / 2;
          const regionTopLeftY = region.position.y - region.height / 2;

          const containBead = contain(
            {
              x: regionTopLeftX,
              y: regionTopLeftY,
              z: 0,
            },
            {
              x: region.position.x + region.width / 2,
              y: region.position.y + region.height / 2,
              z: 0,
            },
            {
              x: bead.position.x,
              y: bead.position.y,
              z: 0,
            }
          );

          if (containBead === false) {
            continue;
          }

          const cellW = region.width / region.columns;
          const cellH = region.height / region.rows;
          const columnIndex = Math.floor(
            (bead.position.x - regionTopLeftX) / cellW
          );
          const rowIndex = Math.floor(
            (bead.position.y - regionTopLeftY) / cellH
          );

          const newIndex = rowIndex * region.columns + columnIndex;

          if (newChildrenGridIds[newIndex] === undefined) {
            newChildrenGridIds[newIndex] = bead.id;
            emptyCount = emptyCount - 1;
          }
        }

        (draft.objects[_singleChange.id] as Region).childrenGridIds.forEach(
          (beadId) => {
            if (beadId !== undefined && draft.objects[beadId] !== undefined) {
              if (newChildrenGridIds.includes(beadId) === false) {
                singleHistoryChange.relatedChanges?.push({
                  id: beadId,
                  properties: {
                    regionId: (draft.objects[beadId] as Bead).regionId,
                  },
                });
              }

              (draft.objects[beadId] as Bead).regionId = undefined;

              updated[beadId] = cloneDeep(draft.objects[beadId]);
            }
          }
        );

        newChildrenGridIds.forEach((beadId) => {
          if (beadId !== undefined) {
            if ((draft.objects[beadId] as Bead).regionId === undefined) {
              singleHistoryChange.relatedChanges?.push({
                id: beadId,
                properties: {
                  regionId: undefined,
                },
              });
            }
            (draft.objects[beadId] as Bead).regionId = region.id;

            updated[beadId] = cloneDeep(draft.objects[beadId]);
          }
        });

        (draft.objects[_singleChange.id] as Region).childrenGridIds = cloneDeep(
          newChildrenGridIds
        );
      }
    }

    if (
      keyNames.includes(autoFillBeadsKey) ||
      keyNames.includes(rowsKey) ||
      keyNames.includes(columnsKey) ||
      keyNames.includes(positionKey)
    ) {
      const region = draft.objects[_singleChange.id] as Region;
      const autoFillBeadsEnabled = region[autoFillBeadsKey] === true;
      const snapBeadsToCellEnabled = region[snapBeadsToCellKey] === true;
      const size = region.rows * region.columns;
      if (region.childrenGridIds.length !== size) {
        console.warn(
          "region childrenIds size have not correct value",
          size,
          region.childrenGridIds.length,
          region.childrenGridIds
        );
        return;
      }

      if (autoFillBeadsEnabled) {
        let nextIdIndex = 0;

        region.childrenGridIds.forEach((id, index) => {
          // @TODO: obecnie nie jest uwzgledniony rotate dla regionu.

          if (id === undefined) {
            const currentRow = Math.floor(index / region.columns);
            const currentCol = Math.floor(index % region.columns);
            const cellW = region.width / region.columns;
            const cellH = region.height / region.rows;
            const offsetX = currentCol * cellW;
            const offsetY = currentRow * cellH;
            const _id =
              _singleChange.relatedCreateBeads?.[nextIdIndex]?.id ?? undefined;
            if (
              _singleChange.relatedCreateBeads !== undefined &&
              _id === undefined
            ) {
              console.warn("id for bead is missing..");
            }
            const bead = buildBead({
              id: _id,
              color: "red",
              regionId: region.id,
            });
            nextIdIndex += 1;

            const startX = region.position.x - region.width / 2;
            const startY = region.position.y - region.height / 2;

            bead.position.x = startX + offsetX + cellW / 2;
            bead.position.y = startY + offsetY + cellH / 2;

            draft.objects[bead.id] = bead;

            region.childrenGridIds[index] = bead.id;

            added.push(cloneDeep(bead));
            singleHistoryChange.relatedCreateBeads!.push(cloneDeep(bead));
          } else {
            const currentRow = Math.floor(index / region.columns);
            const currentCol = Math.floor(index % region.columns);
            const cellW = region.width / region.columns;
            const cellH = region.height / region.rows;
            const offsetX = currentCol * cellW;
            const offsetY = currentRow * cellH;
            const startX = region.position.x - region.width / 2;
            const startY = region.position.y - region.height / 2;
            const posX = startX + offsetX + cellW / 2;
            const posY = startY + offsetY + cellH / 2;
            const oldPosition = cloneDeep(draft.objects[id].position);
            draft.objects[id].position.x = posX;
            draft.objects[id].position.y = posY;
            updated[id] = cloneDeep(draft.objects[id]);
            singleHistoryChange.relatedChanges?.push({
              id,
              properties: {
                position: {
                  x: oldPosition.x,
                  y: oldPosition.y,
                  z: oldPosition.z,
                },
              },
            });
          }
        });
      } else {
        if (snapBeadsToCellEnabled) {
          region.childrenGridIds.forEach((beadId, index) => {
            if (beadId !== undefined) {
              const currentRow = Math.floor(index / region.columns);
              const currentCol = Math.floor(index % region.columns);
              const cellW = region.width / region.columns;
              const cellH = region.height / region.rows;
              const offsetX = currentCol * cellW;
              const offsetY = currentRow * cellH;
              const startX = region.position.x - region.width / 2;
              const startY = region.position.y - region.height / 2;
              const posX = startX + offsetX + cellW / 2;
              const posY = startY + offsetY + cellH / 2;
              const oldPosition = cloneDeep(draft.objects[beadId].position);
              draft.objects[beadId].position.x = posX;
              draft.objects[beadId].position.y = posY;

              updated[beadId] = cloneDeep(draft.objects[beadId]);

              singleHistoryChange.relatedChanges?.push({
                id: beadId,
                properties: {
                  position: {
                    x: oldPosition.x,
                    y: oldPosition.y,
                    z: oldPosition.z,
                  },
                },
              });
            } else {
              const region = draft.objects[_singleChange.id] as Region;

              let foundObject = false;
              Object.keys(draft.objects).forEach((id) => {
                if (foundObject === true) {
                  return;
                }
                if (draft.objects[id].objectType !== ObjectTypes.Bead) {
                  return;
                }

                const bead = draft.objects[id] as Bead;

                if (bead.groupId !== undefined) {
                  return;
                }

                if (bead.regionId !== undefined) {
                  return;
                }

                const currentRow = Math.floor(index / region.columns);
                const currentCol = Math.floor(index % region.columns);
                const cellW = region.width / region.columns;
                const cellH = region.height / region.rows;
                const regionTopLeftX = region.position.x - region.width / 2;
                const regionTopLeftY = region.position.y - region.height / 2;

                const containBead = contain(
                  {
                    x: regionTopLeftX + currentCol * cellW,
                    y: regionTopLeftY + currentRow * cellH,
                    z: 0,
                  },
                  {
                    x: regionTopLeftX + currentCol * cellW + cellW,
                    y: regionTopLeftY + currentRow * cellH + cellH,
                    z: 0,
                  },
                  {
                    x: bead.position.x,
                    y: bead.position.y,
                    z: 0,
                  }
                );

                if (containBead === false) {
                  return;
                }

                (draft.objects[_singleChange.id] as Region).childrenGridIds[
                  index
                ] = bead.id;
                (draft.objects[bead.id] as Bead).regionId = region.id;

                const offsetX = currentCol * cellW;
                const offsetY = currentRow * cellH;
                const startX = region.position.x - region.width / 2;
                const startY = region.position.y - region.height / 2;
                const posX = startX + offsetX + cellW / 2;
                const posY = startY + offsetY + cellH / 2;
                const oldPosition = cloneDeep(draft.objects[bead.id].position);

                draft.objects[bead.id].position.x = posX;
                draft.objects[bead.id].position.y = posY;

                singleHistoryChange.relatedChanges?.push({
                  id: bead.id,
                  properties: {
                    position: {
                      x: oldPosition.x,
                      y: oldPosition.y,
                      z: oldPosition.z,
                    },
                    regionId: undefined,
                  },
                });

                updated[bead.id] = cloneDeep(draft.objects[bead.id]);
                foundObject = true;
              });

              // updated.push(cloneDeep(draft.objects[region.id]));
            }
          });
        }
      }
    }
  }
}
