import React from "react";
import { Link, useHistory, useLocation } from "react-router-dom";
import styled from "styled-components";
import { useContextSelector, useContextUpdate } from "use-context-selector";
import { AppData } from "./AppData";
import { AuthContext } from "./AuthContext";

export const PageHome: React.FC<{
  data: AppData;
}> = () => {
  const dispatch = useContextSelector(AuthContext, (ctx) => ctx.dispatch);
  const history = useHistory();
  const onClickLogout = () => {
    dispatch({
      type: "logout",
    });
    history.replace("/");
  };
  const onClickBoards = () => {
    history.push("/boards");
  };
  return (
    <PageStyles>
      <div>
        <h2>Welcome</h2>
        <button onClick={onClickBoards} className={"btn-light"}>
          Boards
        </button>
        <button onClick={onClickLogout} className={"btn-light"}>
          Logout
        </button>
      </div>
    </PageStyles>
  );
};

const PageStyles = styled.div`
  padding: 1em;
  .btn-light {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: 0.375rem 0.75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 0.25rem;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
      border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
    color: #212529;
    background-color: #f8f9fa;
    border-color: #000;
    margin-bottom: 10px;
    margin-right: 10px;
    cursor: pointer;
  }
  h2 {
    margin-bottom: 0.5rem;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.2;
    color: inherit;
    font-size: 2rem;
  }
`;
