import { getThreeNameFromBeadId } from "./Bead";
import { getThreeNameFromEditBBoxId } from "./EditBBox";
import { AppObject, Editor, ObjectTypes } from "./Editor";
import { getThreeNameFromGroupId } from "./Group";
import { getThreeNameFromPreviewProject3DId } from "./PreviewProject3D";
import { getThreeNameFromRegionId } from "./Region";

export const getStateObjectFromObjectId = (
  objectId: string,
  objectType: ObjectTypes
) => {
  let name: string | undefined = undefined;

  if (objectType === ObjectTypes.Bead) {
    name = getThreeNameFromBeadId(objectId);
  } else if (objectType === ObjectTypes.Region) {
    name = getThreeNameFromRegionId(objectId);
  } else if (objectType === ObjectTypes.Group) {
    name = getThreeNameFromGroupId(objectId);
  } else if (objectType === ObjectTypes.PreviewProject3D) {
    name = getThreeNameFromPreviewProject3DId(objectId);
  } else if (objectType === ObjectTypes.SceneEditBBox) {
    name = getThreeNameFromEditBBoxId(objectId);
  }

  if (name === undefined) {
    console.warn("name is empty for id", objectId);
    return undefined;
  }
  return name;
};
