  getMoveObjectsAction(objectIds: string[], diffX: number, diffY: number) {
    const store = getStore();

    const ids: string[] = [];
    const change: ChangeObjectsProperty["payload"]["changes"] = [];
    objectIds.forEach((objectId) => {
      const childrenIds = this.getIdsFromGroupOrInGroup(objectId);
      if (store.objects[objectId] !== undefined) {
        if (ids.includes(objectId) === false) {
          ids.push(objectId);
          change.push({
            id: objectId,
            properties: {
              position: {
                x: store.objects[objectId].position.x + diffX,
                y: store.objects[objectId].position.y + diffY,
                z: store.objects[objectId].position.z,
              },
            },
          });
        }
      }
      childrenIds.forEach((id) => {
        if (store.objects[id] !== undefined) {
          if (ids.includes(objectId) === false) {
            ids.push(objectId);
            change.push({
              id: id,
              properties: {
                position: {
                  x: store.objects[id].position.x + diffX,
                  y: store.objects[id].position.y + diffY,
                  z: store.objects[objectId].position.z,
                },
              },
            });
          }
        }
      });
    });

    const action: ChangeObjectsProperty = {
      name: AppEventType.ChangeObjectsProperty,
      payload: {
        changes: change,
      },
    };

    return action;
  }

  getSelectionForSingleObjectIdAction(id: string) {
    const store = getStore();
    const change: ChangeSelectObjects["payload"]["change"] = [];

    store.selectedIds.forEach((_id) => {
      const childrenIds = this.getIdsFromGroupOrInGroup(_id);
      change.push({
        id: _id,
        isSelected: false,
      });
      childrenIds.forEach((__id) => {
        change.push({
          id: __id,
          isSelected: false,
        });
      });
    });

    change.push({
      id,
      isSelected: true,
    });
    const childrenIds = this.getIdsFromGroupOrInGroup(id);
    childrenIds.forEach((_id) => {
      change.push({
        id: _id,
        isSelected: true,
      });
    });

    const action: ChangeSelectObjects = {
      name: AppEventType.ChangeSelectObjects,
      payload: {
        change,
      },
    };

    return action;
  }

  getSelectionAppendAction(
    objectIds: string[],
    _newIsSelected?: boolean,
    _addWithIsLock?: boolean
  ) {
    const store = getStore();
    const change: ChangeSelectObjects["payload"]["change"] = [];
    const addWithIsLock = _addWithIsLock ?? false;

    // console.log("**** getSelectionAppendAction ---- 00 ", _addWithIsLock);

    objectIds.forEach((objectId) => {
      const isLocked =
        store.objects[objectId] !== undefined &&
        store.objects[objectId].isLocked === true;

      // console.log(
      //   "**** getSelectionAppendAction ---- ",
      //   objectId,
      //   addWithIsLock === true,
      //   addWithIsLock === false && isLocked === false,
      //   addWithIsLock === true ||
      //     (addWithIsLock === false && isLocked === false),
      //   "IsLocked: ",
      //   isLocked
      // );
      if (
        addWithIsLock === true ||
        (addWithIsLock === false && isLocked === false)
      ) {
        const isSelected = store.selectedIds.includes(objectId) === true;
        const newSelected =
          _newIsSelected === undefined ? !isSelected : _newIsSelected;
        const childrenIds = this.getIdsFromGroupOrInGroup(objectId);
        change.push({
          id: objectId,
          isSelected: newSelected,
        });
        childrenIds.forEach((id) => {
          change.push({
            id: id,
            isSelected: newSelected,
          });
        });
      }
    });

    const action: ChangeSelectObjects = {
      name: AppEventType.ChangeSelectObjects,
      payload: {
        change,
      },
    };

    return action;
  }

