  moveSingleObject(clientX: number, clientY: number) {
    // const { getState } = appStore;
    // const state = getState();
    const store = getStore();

    const { x, y } = screenToWorld(clientX, clientY);
    this.mouseDragHelper.onMove(x, y);

    const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
    const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;

    const clicked = this.intersects[0].object.userData;
    const objectId = clicked.id;
    if (objectId === undefined) {
      console.warn("objectId not found in clicked item", clicked);
      return;
    }
    const obj = store.objects[objectId];
    if (obj === undefined) {
      console.warn("objectId not found in app store", objectId, clicked);
      return;
    }
    const name = getStateObjectFromObjectId(objectId, obj.objectType);
    if (name === undefined) {
      return;
    }
    const object = appThree.scene.getObjectByUserDataProperty("name", name);
    if (object === undefined) {
      console.warn("object not found in app state for id", objectId, clicked);
      return;
    }

    if (store.objects[objectId].objectType === ObjectTypes.Region) {
      const region = store.objects[objectId] as Region;
      if (region.snapBeadsToCell === true) {
        if (region.childrenGridIds.length > 0) {
          region.childrenGridIds.forEach((id) => {
            if (id !== undefined && store.objects[id] !== undefined) {
              const bead = store.objects[id];
              if (bead === undefined) {
                console.warn("object not exists in app", objectId);
                return;
              }
              const name = getStateObjectFromObjectId(bead.id, bead.objectType);
              if (name === undefined) {
                return;
              }
              const child_object = appThree.scene.getObjectByUserDataProperty(
                "name",
                name
              );
              if (child_object === undefined) {
                console.warn(
                  "object not found in app scene for id",
                  objectId,
                  name
                );
                return;
              }
              child_object.position.x = store.objects[id].position.x + diffX;
              child_object.position.y = store.objects[id].position.y + diffY;
            }
          });
        }
      }
    }

    // console.log("przesuwam...", diffX, diffY);
    object.position.x = store.objects[objectId].position.x + diffX;
    object.position.y = store.objects[objectId].position.y + diffY;

    this.updateUIAtMove(false);
  }

  moveObjectGroup(clientX: number, clientY: number) {
    // const { dispatch, getState } = appStore;
    // const state = getState();
    const store = getStore();

    const { x, y } = screenToWorld(clientX, clientY);

    this.mouseDragHelper.onMove(x, y);

    const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
    const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;

    const clicked = this.intersects[0].object.userData;
    let objectId = clicked.id;
    if (objectId === undefined) {
      console.warn("objectId not found in clicked item", clicked);
      return;
    }
    let group = store.objects[objectId];
    if (group === undefined) {
      console.warn("objectId not found in app store", objectId);
      return;
    }

    // const isGroup =
    //   group.objectType !== undefined && group.objectType === ObjectTypes.Group;

    const isGroupChild =
      group[groupIdKey] !== undefined && group[groupIdKey].length > 0;

    if (isGroupChild) {
      objectId = group[groupIdKey];
      group = store.objects[objectId];
    }

    const name = getStateObjectFromObjectId(objectId, group.objectType);
    if (name === undefined) {
      return;
    }
    const object = appThree.scene.getObjectByUserDataProperty("name", name);
    if (object === undefined) {
      console.warn("object not found in app state for id", objectId);
      return;
    }
    object.position.x = store.objects[objectId].position.x + diffX;
    object.position.y = store.objects[objectId].position.y + diffY;

    (group as Group).childrenIds.forEach((id) => {
      const children: AppObject | undefined = store.objects[id];
      if (children === undefined) {
        console.warn("object not exists in app", id);
        return;
      }
      const name = getStateObjectFromObjectId(children.id, children.objectType);
      if (name === undefined) {
        return;
      }
      const child = appThree.scene.getObjectByUserDataProperty("name", name);
      if (child === undefined) {
        console.warn("object not found in app scene for id", id, name);
        return;
      }
      child.position.x = store.objects[id].position.x + diffX;
      child.position.y = store.objects[id].position.y + diffY;
    });

    this.updateUIAtMove(false);
  }

