  moveAllSelectedObjects(clientX: number, clientY: number) {
    // const { dispatch, getState } = appStore;
    // const state = getState();

    const { x, y } = screenToWorld(clientX, clientY);
    this.mouseDragHelper.onMove(x, y);

    const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
    const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;
    // console.log("-------------- ", clientX, clientY, x, y, diffX, diffY);

    const store = getStore();

    store.selectedIds.forEach((objectId) => {
      const obj: AppObject | undefined = store.objects[objectId];
      if (obj === undefined) {
        console.warn("object not exists in app", objectId);
        return;
      }
      const name = getStateObjectFromObjectId(obj.id, obj.objectType);
      if (name === undefined) {
        return;
      }
      const object = appThree.scene.getObjectByUserDataProperty("name", name);
      if (object === undefined) {
        console.warn("object not found in app scene for id", objectId, name);
        return;
      }

      if (store.objects[objectId].objectType === ObjectTypes.Region) {
        if (appThree.additionalData.selectedBBox !== undefined) {
          for (let i = appThree.scene.children.length - 1; i >= 0; i--) {
            if (appThree.scene.children[i].layers.mask === 4) {
              const name = appThree.scene.children[i].userData["name"];

              appThree.scene.children[i].position.x =
                appThree.additionalData.selectedBBox[name].x + diffX;

              appThree.scene.children[i].position.y =
                appThree.additionalData.selectedBBox[name].y + diffY;
            }
          }
        }
        const region = store.objects[objectId] as Region;
        if (region.snapBeadsToCell === true) {
          if (region.childrenGridIds.length > 0) {
            region.childrenGridIds.forEach((id) => {
              if (id !== undefined && store.objects[id] !== undefined) {
                const bead = store.objects[id];
                if (bead === undefined) {
                  console.warn("object not exists in app", objectId);
                  return;
                }
                const name = getStateObjectFromObjectId(
                  bead.id,
                  bead.objectType
                );
                if (name === undefined) {
                  return;
                }
                const child_object = appThree.scene.getObjectByUserDataProperty(
                  "name",
                  name
                );
                if (child_object === undefined) {
                  console.warn(
                    "object not found in app scene for id",
                    objectId,
                    name
                  );
                  return;
                }
                child_object.position.x = store.objects[id].position.x + diffX;
                child_object.position.y = store.objects[id].position.y + diffY;
              }
            });
          }
        }
      }

      object.position.x = store.objects[objectId].position.x + diffX;
      object.position.y = store.objects[objectId].position.y + diffY;
    });

    this.updateUIAtMove(false);
    // if (state.ui.editBBox !== undefined) {
    //   const objectId = state.ui.editBBox.id;
    //   const name = getStateObjectFromObjectId(
    //     objectId,
    //     ObjectTypes.SceneEditBBox
    //   );
    //   if (name === undefined) {
    //     return;
    //   }

    //   const object = appThree.scene.getObjectByUserDataProperty("name", name);
    //   if (object === undefined) {
    //     console.warn("object not found in app scene for id", objectId, name);
    //     return;
    //   }

    //   object.visible = false;
    // }
  }

  updateUIAtMove(visible: boolean) {
    if (visible === false) {
      this.hideTopBar();
    } else {
      this.showTopBar();
    }
  }

  hideTopBar() {
    const selector = ".selected-objects-top-bar";
    const topBar = document.querySelector(selector) as HTMLDivElement | null;
    if (topBar === null) {
      return;
    }
    topBar.style.display = "none";
  }

  showTopBar() {
    const selector = ".selected-objects-top-bar";
    const topBar = document.querySelector(selector) as HTMLDivElement | null;
    if (topBar === null) {
      return;
    }
    topBar.style.display = "block";
  }

