import {
  clearThreeScene,
  addLightFromUI,
  addGridFromUi,
  addObjectByObjectType,
} from "./threejs/eventEmitterHandlers";
import { appThree } from "./threejs/renderThreejs";

export const prepareBoard = (id: string) => {
  return getProjectData(id).then((data) => {
    return new Promise((resolve, reject) => {
      clearThreeScene();

      const store = getStore();

      const newStore = produce(store, (draft) => {
        draft.board = cloneDeep(emptyStore.board);
        draft.history = cloneDeep(emptyStore.history);
        draft.objects = cloneDeep(emptyStore.objects);
        draft.selectedIds = cloneDeep(emptyStore.selectedIds);
        draft.ui = cloneDeep(emptyStore.ui);

        if (data.objects !== undefined && data.objects.length > 0) {
          data.objects.forEach((object) => {
            draft.objects[object.id] = object;
          });
        }

        draft.board.name = data.board?.name ?? "";
        draft.board.uuid = data.board?.uuid ?? "";
        draft.board.canEdit = true;
        draft.ui.blockThreejs.loading = false;
      });

      setStore(newStore);

      store.ui.lights.forEach((light) => {
        addLightFromUI(light, appThree.scene);
      });

      if (store.ui.grid) {
        addGridFromUi(store.ui.grid);
      }

      Object.keys(newStore.objects).forEach((id) => {
        addObjectByObjectType(newStore.objects[id]);
      });

      refreshReactState(newStore);

      testRegions();
      resolve(undefined);
    });
  });
};

export const clearBoard = (id: string) => {
  return new Promise<void>((resolve) => {
    clearThreeScene();
    const store = getStore();
    const newStore = produce(store, (draft) => {
      draft = cloneDeep(emptyStore);
    });

    refreshReactState(newStore);
    resolve();
  });
};

